export default {
    //
    version: "1.0.0",
    webRoot: {
        //
        idm:"idm",
        //
        default: "/DreamWeb/",
        //./../idm//DreamWeb/idm/
        htmlDir: "./",
        //
        assetsDir: "./static/",
        /**
         * (idm_modules)
         * index.htmlDreamWeb/idm/A  ../../idm_modules//DreamWeb/idm_modules/
         * index.htmlDreamWeb/ ./idm_modules//DreamWeb/idm_modules/
         */
        moduleDir: "./idm_modules/",
        /**
         * 
         * DreamWebp1000,
         * 
         */
        platformNo:"p1000",
        /**
         * 
         * platformNo
         * 
         */
        projectNo:"p1000",
        /**
         * 
         */
        moduleAbsoluteDir:"/{projectNo}/idm/idm_modules/"
    },
    //
    pageSchema: {
        //JSONdiskdatabase
        saveType: "database",
        //json{version}{pageid}
        jsonDir: "/{projectNo}/idm/idm_pageschema/{pageid}/{version}.json"
    },
    // apimockdata
    api: {
        /**
         * /ctrl/idm/api/fetchComponentMarketData
         * 
         */
        componentMarketUrl: "",
        /**
         * /ctrl/idm/api/fetchPageVersionList)
         * 
         * pageidID
         */
        pageVersionListUrl: "/ctrl/idm/api/fetchPageVersionList",
        /**
         * /ctrl/idm/api/fetchPageSettingData)
         * 
         * pageidID
         * versionsaveTypediskdatabase
         * savetype:==JSONdiskdatabase
         */
        pageSettingDataUrl: "/ctrl/idm/api/fetchPageSettingData",
        /**
         * /ctrl/idm/api/fetchCustomModuleSettingData)
         * 
         * pageidID
         * schemaType01
         */
         customModuleSettingDataUrl: "/ctrl/idm/api/fetchCustomModuleSettingData",
        /**
         * (/ctrl/idm/api/savePageSchema)
         * 
         * pageidID
         * pageSettingDatajsonjson
         * version
         * author
         * remark
         * isUse10
         * traceList,[{actionType:pageidIDpageVersionopDateopTimeopContent}]
         * projectNo
         */
        savePageSettingUrl: "/ctrl/idm/api/savePageSchema",
        /**
         * (/ctrl/idm/api/saveCustomModule)
         * 
         * pageidID
         * title
         * previewImgJsonJSON
         * moduleType01
         * moduleIconfont
         * moduleContainerCount
         * pageSettingDatajson
         * author
         * remark
         * traceList,[{actionType:pageidIDpageVersionopDateopTimeopContent}]
         * projectNo
         */
        saveCustomModuleUrl:"/ctrl/idm/api/saveCustomModule",
        /**
         * 
         * 
         * typedev_uploadimage_ctrl
         * filejava((MultipartHttpServletRequest)HttpServletRequest).getFile("file")
         * ...IDM.setting.webRoot
         * 
         * {
         *  fileName: "tab.png"
         *  filePath: "/upload/idmfiles/f22081da-9410-40bc-afa0-6b3106c45c1c.png"
         *  fileSize: "218KB"
         * }
         */
        uploadFileServerUrl: "/ctrl/idm/api/fileupload",
        /**
         * base64
         * 
         * typedev_uploadimage_ctrl
         * database64
         * suffix: jpeg, png, webp
         * ...IDM.setting.webRoot
         * 
         * {
         *  fileName: "tab.png"
         *  filePath: "/upload/idmfiles/f22081da-9410-40bc-afa0-6b3106c45c1c.png"
         *  fileSize: "218KB"
         * }
         */
        saveCropperBase64ImageUrl:"/ctrl/idm/api/saveBase64ToImageFile",
        /**
         * 
         * 
         * pageidID
         * startIndex
         * pageNumber
         * 
         * [{
         *  id:ID
         *  actionType: 
         *  pageId:ID
         *  pageVersion:
         *  opDate:
         *  opTime:
         *  opContent:
         * }]
         */
        developTraceListUrl:"/ctrl/idm/api/fetchDevelopTraceListInfo",
        /**
         * 
         * 
         * urlData:url,
         * pageidID
         * 
         */
        applicationInfoUrl:"/ctrl/idm/api/fetchApplicationInfo",
        /**
         * 
         * 
         * urlData:url,
         * pageidID
         * 
         */
        userInfoUrl:"/ctrl/idm/api/fetchUserInfo",
        /**
         * 
         * 
         * urlData:url,
         * pageidID
         * version
         * customData
         * 
         * (datanull)
         */
        saveUserCustomizationUrl:"/ctrl/idm/api/saveUserCustomization",
         /**
          * 
          * 
          * urlData:url,
          * pageidID
          * version
          * 
          * 
          * {
          *    "code":"200",
          *    "message":"",
          *    "data":{
          *      id:"",
          *      idmId:"",
          *      pageId:"",
          *      pageVersion:"",
          *      customData:"customData,datanull/ctrl/idm/api/fetchPageSettingData",
          *      userId:"",
          *    }
          * }
          */
        userCustomizationUrl:"/ctrl/idm/api/fetchUserCustomization",
        /**
         * URl
         * 
         * urlData:url,
         * pageIdID,
         * moduleData:
          * 
          * {
          *    
          * }
         */
        saveFormsMetaDataUrl:"/ctrl/idm/api/saveFormsMetaData",
        /**
         * url/ctrl/idm/console/fetchAllCodePackageList
         * 
         * {
         * }
         */
        allCodepackageUrl:"/ctrl/idm/console/fetchAllCodePackageList",
        /**
         * url/ctrl/idm/console/fetchMarketModuleAttrUserSetData
         * 
         * [{
         * id:"",
         * attrCode:"",
         * attrData:"",
         * ...
         * }]
         */
        dynamicAttributeListUrl:"/ctrl/idm/console/fetchMarketModuleAttrUserSetData",
    },
    /**
     * mockdataAPI
     * $webRoot > assetsDir
     * ~webRoot > htmlDir
     * @webRoot > moduleDir
     * ./../http://https://
     * webRoot > default
     */
    mockurl: {
        /**
         * 
         */
        componentMarketUrl: "$/mockdata/ComponentMarketData.json",
        /**
         * 
         */
        pageVersionListUrl: "$/mockdata/PageVersionListData.json",
        /**
         * 
         */
        pageSettingDataUrl: "$/mockdata/pageSettingDataData.json",
        /**
         * 
         */
         customModuleSettingDataUrl: "$/mockdata/customModuleSettingDataData.json",
         /**
          * 
          */
         allCodepackageUrl:"$/mockdata/AllCodePackageListData.json",
    },
    //
    develop: {
        /**
         * IDM
         * $webRoot > assetsDir
         * ~webRoot > htmlDir
         * @webRoot > moduleDir
         * ./../http://https://
         * webRoot > default
         */
        schoolsUrl: "http://school.yunit.cn",
        /**
         * 
         */
        devComponentUrl: "https://yunit-code.github.io/zh/moduledevelop/introduce.html",
        /**
         * 
         */
        helpDocumentUrl: "https://yunit-code.github.io/zh/",
        /**
         * 
         */
         documentManageUrl: "./index.html#/document",
        /**
         * 
         * {
                key: 3,
                width: px,
                height: px,
                showText: ,
                type: 
            }
         * type:-1 
         * type:3  
         */
        dragAreaSizeList: [
            {
                key: '3',
                width: 1366,
                height: 768,
                showText: '1366*768',
                type: 3
            },
            {
                key: '4',
                width: 1920,
                height: 1080,
                showText: '1920*1080',
                type: 3
            },
            {
                key: '5',
                width: 375,
                height: 667,
                showText: 'iphone6/7/8',
                type: 3
            },
            {
                key: '6',
                width: 414,
                height: 736,
                showText: 'iphone6/7/8 Plus',
                type: 3
            },
            {
                key: '7',
                width: 768,
                height: 1024,
                showText: 'iPad(768*1024)',
                type: 3
            },
            {
                key: '8',
                width: 1024,
                height: 768,
                showText: 'iPad(1024*768)',
                type: 3
            }
        ],
        /**
         * keyurldragSizeKey
         * 1
         */
        dragAreaSizeDefaultKey:"1",
        /**
         * 
         * grid
         * list
         */
        componentMarketListDefaultType:"grid",
        /**
         * userInfoUrl
         */
        openFetchUserInfo:true
    },
    //
    document:{
        /**
         * 
         * idm/idm_modules/basic/1.0.0/
         * static/doc/
         * index.md
         */
        codepackageDocumentPath:"static/doc/",
        /**
         * 
         * idm/idm_modules/basic/1.0.0/
         * static/doc/components/
         * .mdIButton.md
         */
        moduleDocumentPath:"static/doc/components/",
        /**
         * md
         * 
         * mdPath:md
         * type:codemodule
         * moduleClassName:
         * mdContent:md
         */
        saveMarkDownFileUrl:"/ctrl/idm/api/saveDocumentMdFile"
    },
    //
    applications:{
        /**
         * 
         */
        defaultLoginPageUrl:"/ctrl/login",
        /**
         * class
         */
        themeNamePrefix:"idm-theme-",
        /**
         * url
         */
        moduleControlCenterSettingUrl:"~/index.html#/preview/idm_control_setting_panel_001",
        /**
         * true
         */
        useProjectNoSetting:true,
        /**
         * 
         */
        moduleAttrFolderName:"attributes",
        /**
         * 
         */
        fontFamilyList:[
            {
                value:"arial, helvetica, 'microsoft yahei'",
                label:"Arial, Helvetica, "
            },
            {
                value:"arial, helvetica, simhei",
                label:"Arial, Helvetica, "
            },
            {
                value:"'comic sans ms', 'microsoft yahei'",
                label:"Comic Sans MS, "
            },
            {
                value:"'comic sans ms', simhei",
                label:"Comic Sans MS, "
            },
            {
                value:"impact, 'microsoft yahei'",
                label:"Impact, "
            },
            {
                value:"impact, simhei",
                label:"Impact, "
            },
            {
                value:"'lucida sans unicode', 'microsoft yahei'",
                label:"Lucida Sans Unicode, "
            },
            {
                value:"'lucida sans unicode', simhei",
                label:"Lucida Sans Unicode, "
            }
        ],
        /**
         * 
         */
        commonRequireJsCss:[
            "/resource/js/public.js",
            "/resource/js/dreamweb-core.js",
            "/websocket/webscoket.js"
        ],
        /**
         * 
         */
        openIdmCoreLoadStyle:true,
        /**
         * 
         */
        idmCoreLoadColor:"#1890ff",
        /**
         * 
         */
        defaultPreviewLoadColor:"#1890ff",
        /**
         * SDK
         */
        defaultOpenWeixinJSSDK:true
    },
    //websocket
    websocket:{
        /**
         * websocketwebsocketcommonRequireJsCss
         */
        websocketInitEventName:"initDreamSocketClient",
        /**
         * websocketIDMIDMwebsocket
         */
        websocketOpenEventName:"onWebSocketOpen",
        /**
         * websocketIDMIDMwebsocketwebsocket
         */
        websocketMessageEventName:"onWebSocketMessage"
    },
    //
    weixin:{
        /**
         * js-sdk
         */
        requireJsCss:[
            "http://res.wx.qq.com/open/js/jweixin-1.2.0.js"
        ],
        /**
         * JSJShttps://developer.work.weixin.qq.com/document/path/90513
         * 'launch3rdApp', 'request3rdApp', 'onHistoryBack', 'hideAllNonBaseMenuItem', 'hideOptionMenu', 'openUrl', 'closeWindow'
         */
        jsApiList: ['launch3rdApp', 'request3rdApp', 'onHistoryBack', 'hideAllNonBaseMenuItem', 'hideOptionMenu', 'openUrl', 'closeWindow'],
        /**
         * ,apialertpclogpc
         */
        debug:false,
        /**
         * 
         * /tencentCustomize/getConfig
         * 
         * {
         *   code:"200",
         *   message:"",
         *   type:"success",
         *   data:{
         *      appId:"",
         *      timestamp:"",
         *      nonceStr:"",
         *      signature:"",
         *   }
         * }
         */
        signatureUrl:"/tencentCustomize/getConfig"
    }
}